.main-div{
  width: 100%;
  height: 100vh;
  background-color: #6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: samplee 2s 0.1s infinite alternate;

}
h1{
  color: white;
  background: transparent;
  background-color:#8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
  border-radius: 0em 12em;
  animation: sample 2s 0.1s infinite alternate;
}

.todoList{
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
  overflow: scroll;
}
.form input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}
.form button{
  min-height: 40px;
  width: 40px;
  border-radius: 600%;
  border-color: transparent;
  background-color:#8566aa;
  color: #fff;
  font-style: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
  font-size: 35px;
}
button:hover{
  background-color:#370e69;
}

.fas{
  color: red;
  padding: 10px;
}
.listText{
  text-transform: capitalize;
  color: #8566aa;
  padding-left: 1px;
  width: 65%;
  float: left;
  font-size: 23px;
  font-weight: bolder;
}
.flex{
  margin-bottom: 20px;
}
.button-del{
  min-height: 40px;
  width: 74px;
  border-color: transparent;
  background-color:white;
  color: black;
  font-style: 40px;
  border: 1px solid #8566aa;
  outline: none;    font-size: 15px;
  margin-top: 12px;
  font-size: 16px;
  font-weight: bolder;
}
.button-del:hover{
  background-color: #8566aa;
  color: white;
}
@media (max-width: 768px){
  .todoList{
      width: 55%;
  }
}
@keyframes sample {
0%{ background-color: black;  }
50%{ background-color: #370e69; color: white; }
100%{ background-color: #8566aa; color: white; }
}
@keyframes samplee {
0%{ background-image:url("pakistan.png") ;  }
50%{ background-image:url("chand.png") ;  }
75%{ background-image:url("bagh.png") ;  }
100%{ background-image:url("dil.png") ;  }
}
